  async function  sellnft(tokenid){
    setdis(true)
    let useridc = window.nearwallet.getAccountId().split(".")[0]
    let user = {
      contract_token_id: window.cidparam+"."+tokenid,
      price: document.getElementById(tokenid).value,
    }
    let price= document.getElementById(tokenid).value
    const minttcontract = new nearAPI.Contract(
      window.nearwallet.account(), // the account object that is connecting
     window.cidparam,
      {
        // name of contract you're connecting to
        viewMethods: [""], // view methods do not change state but usually return a value
        changeMethods: ["nft_approve"], // change methods modify state
        sender: window.nearwallet.account(), // account object to initialize and sign transactions.
      }
    );
    let r = await axios.post('https://nfteclectica.herokuapp.com/nftsale', user);
    console.log(r)
    if (r.status != 200){
      alert("Failure in saving Data")
    }
    console.log(r)
    price = utils.format.parseNearAmount(price);
    let sale_conditions = price; // updated this line to match expected format for cross contract call
    
    let args = {
      token_id: tokenid,
      account_id: 'shopp.eclectica.testnet',
      msg: JSON.stringify({ sale_conditions })
    }
    
    let GAS = "200000000000000";
    let deposit = utils.format.parseNearAmount('0.6');
    
    await minttcontract.nft_approve(args, GAS, deposit);
    return
  }
  const [happen,sethappen] = React.useState(
    "Calling Smart Contract"
  )
  const [sent,setsent] = useState(false);

  async function fetchImage(url){
    console.log(url)
    url = url.split('/');
   let link = url[2];
   let back = url[url.length-1];

   let data = await axios.get(`https://${link}.ipfs.dweb.link/${back}`)
   console.log(data)
   url = data.data.image.split("/");
   console.log("this is url"+ url)
   link = url[2]
   back = url[url.length-1];
   let urll = `https://${link}.ipfs.dweb.link/${back}`


   return urll



 }

 async function fetcher(){
   
await start()

 }
 async function asyncForEach(array, callback) {
  for (let index = 0; index < array.length; index++) {
    await callback(array[index], index, array);
  }
}


 const start = async () => {
  let todoss = []
  await asyncForEach(todos, async (elements) => {
    console.log(elements)
    elements.url = await fetchImage(elements.metadata)
    console.log(elements.url)
    todoss.push(elements)
    console.log(elements)
  });
  setTodos(todoss);
  console.log('Done');
  setsent(true)
}


 
